<template>
	<view class="tabbar-container flex items-center">
		<!-- isRound是中间凸出样式的标志，用来判断当前子级是否是凸出样式的 -->
		<view :class="!item.isRound ? 'square' : 'is-square'" v-for="(item, index) in tabbarList" :key="index" @click="tabbarChange(index, item)">
			<!-- 不是凸出的子级 -->
			<template v-if="!item.isRound">
				<view class="item-top flex justify-center flex-col">
					<u-image :src="active == index ? item.selectedIconPath : item.iconPath" :width="item.width"
						:height="item.height"></u-image>
				</view>
				<view class="item-bottom" :class="{'active': active === index}">
					<text>{{ item.text }}</text>
				</view>
			</template>
			<!-- 凸出的子级 -->
			<template v-else>
				<view class="flex flex-col justify-center items-center center-round">
					<view class="flex flex-col justify-center items-center">
						<u-image :src="active == index ? item.selectedIconPath : item.iconPath" :width="item.width"
							:height="item.height"></u-image>
						<text :class="{'round-active': active === index}">{{ item.text }}</text>
					</view>
				</view>
			</template>
		</view>
	</view>
</template>
<script>
	export default {
		name: 'Tabbar',
		props: {
			tabbarIndex: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				// tabbar列表，配置自定义用到的属性字段
				tabbarList: [{
						pagePath: "/pages/Guqi/index",
						iconPath: "/static/tabbar/guqi.png",
						selectedIconPath: "/static/tabbar/guqi-s.png",
						text: "古奇",
						width: '45rpx',
						height: '41rpx',
						isRound: false
					},
					{
						pagePath: "/pages/Customize/Customize",
						iconPath: "/static/tabbar/jiutong.png",
						selectedIconPath: "/static/tabbar/jiutong-s.png",
						text: "定制",
						width: '120rpx',
						height: '120rpx',
						isRound: true
					},
					{
						pagePath: "/pages/Mine/Mine",
						iconPath: "/static/tabbar/center.png",
						selectedIconPath: "/static/tabbar/center-s.png",
						text: "我的",
						width: '40rpx',
						height: '41rpx',
						isRound: false
					},
				],
				active: 0,
				isRound: false,
			}
		},
		mounted() {
			this.active = this.tabbarIndex
		},
		methods: {
			tabbarChange(index, item) {
				this.$emit('updateTabbar', index)
				uni.switchTab({
					url: item.pagePath
				});
			}
		}
	}
</script>

