	.tabbar-container {
		width: 100%;
		height: 132rpx;
		position: fixed;
		bottom: 0;
		.square {
			width: 100%;
			height: 130rpx;
			background: #FFFFFF;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			.round-active {
				color: #207BDB;
			}
		}
		.square .item-top {
			width: 45rpx;
			height: 45rpx;
		}
		.is-square {
			width: 100%;
			height: 130rpx;
			background: #FFFFFF;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			.round-active {
				color: #207BDB;
			}
		}
		.center-round {
			width: 203rpx;
			height: 130rpx;
			background: url('data:image/png;base64,');
			background-position: center top;
			background-size: 100% 37rpx;
			background-repeat: no-repeat;
			position: absolute;
			margin-top: -72rpx;

			text {
				font-weight: 500;
				font-size: 20rpx;
				color: #555B66;
				position: absolute;
				top: 117rpx;
			}
		}

		.item-bottom {
			font-weight: 500;
			font-size: 20rpx;
			color: #555B66;
			margin-top: 14rpx;
		}

		.active {
			color: #207BDB;
		}
	}
</style>
